nav ul {
  padding: 0;
  list-style: none;
  /*  background: #fff;  */
}
@media only screen and (max-width: 660px) {
  nav ul {
    margin-top: 0px;
    background-color: white;
    /*  background: rgba(255, 255, 255, 0.5);  */
  }
}

nav ul li {
  display: inline-block;
  position: relative;
  line-height: 14px;
  text-align: left;
  font-size: 0.85rem;
}
@media only screen and (max-width: 660px) {
  nav ul li {
    display: inline;
    line-height: 140%;
    font-size: 1rem; /*   font-size: 1.2rem;  */
    margin-left: 0%;
  }
}

nav ul li a {
  display: block;
  padding: 8px 20px;
  color: #000;
  text-decoration: none;
  /*     font-weight:normal; */
}
@media only screen and (max-width: 660px) {
  nav ul li a {
    display: block;
    padding: 8px 20px;
    text-decoration: none;
    border-bottom-style: dotted;
    border-bottom-width: 1px;
    border-bottom-color: #000;
  }
}

nav ul li ul li a {
  font-weight: normal;
  border-bottom-style: dotted;
  border-bottom-width: 1px;
  border-bottom-color: #000;
}

nav ul li ul li {
  width: 100%;
}

/*  nav ul li ul.sub-menu { */
nav ul li ul {
  width: 100%; /* Set width of the sub-menu */

  background: rgba(255, 255, 255, 100);
  position: absolute;
  display: none;
  z-index: 999;
  left: 0;
  width: 250px;
}
@media only screen and (max-width: 660px) {
  nav ul li ul {
    min-width: 100%;
    /* Set width of the sub-menu */
    display: inline;
    position: relative;
    background: rgba(255, 255, 255, 100);
  }
}

/*nav ul li:hover ul.sub-menu */

nav ul li:hover ul {
  display: block;
  /* Display the sub-menu */
}

/*
nav ul li ul.sub-menu li {
  display: block; }
*/

nav ul li a:hover {
  color: #fff;
  background: #939393;
  /* die einzelnen Link Hintergrund farbe bei hover */
}

