/* HIER KOMMT DIE GALERIE */

/* section */
.bild {
  /*  width:100%;
*/
}

.schalterdiv {
  width: 40px;
  height: 200px;
  z-index: 2;
  align-items: center;
}

div .schalterdiv a {
  display: -webkit-flex;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  height: 100%;
  height: 100%;
}
div .schalter-1svg a:hover {
  border-radius: 0em 4em 4em 0em;
  background: rgba(0, 0, 0, 0.2);
}

div .schalter-2svg a:hover {
  border-radius: 4em 0em 0em 4em;
  background: rgba(0, 0, 0, 0.2);
}

.schalter-1svg {
  margin-right: -40px;
  border-radius: 12px;
}

.schalter-2svg {
  margin-left: -40px;
}

.bilderdiv {
  width: 100%;
  z-index: 0;
}

.bildgalerie {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
}

@media only screen and (max-width: 660px) {
  .bildgalerie {
    margin-top: 10px;
  }
}

.bilderdiv {
  width: 100%;
  z-index: 0;
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.bildzaehler {
  width: 100%;
  height: 40px;
  margin-top: -80%;

  position: relative;
  top: -35px;

  /* ausgleich fuer doppeltes bild */
  z-index: 2;

  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  -ms-flex-align: center;
  -webkit-align-items: center;
  -webkit-box-align: center;

  /* text-align:center; */
  -ms-flex-pack: center;
  justify-content: center;
}

/* Nur Firefox: Hack-  2 Bilder Ã¼bereinander mÃ¼ssen in der HÃ¶he korrigiert werden */
@-moz-document url-prefix() {
  .bildzaehler {
    top: 0px;
    margin-top: -50px;
  }
}

.bildtitel {
  z-index: 2;

  width: auto;
  position: absolute;

  /* background:rgba(255,255,255,0.64);    */
  text-align: left;

  margin-bottom: -60px;
  padding-left: 0px;
  padding-top: 20px;

  font-size: 1.1rem;
  color: #fff;
}
@media only screen and (max-width: 660px) {
  .bildtitel {
    font-size: 0.9rem;
  }
}

/* bildtitel wird mit span eingschlossen fÃ¼r streifen-background */
